/* global/Forms.scss*/
/* ===============================================================
 *                       CUSTOM RESET
 * =============================================================== */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
textarea:-webkit-autofill:active,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus,
select:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-box-shadow: 0 0 0px 1000px #fff inset; }

/* global/Forms.scss*/
/* ===============================================================
 *                MATERIALIZE.CSS CUSTOMIZATIONS
 * =============================================================== */
.suffix {
  float: right;
  width: 1em;
  font-size: 2rem;
  transition: color .2s;
  position: absolute;
  top: 5px;
  right: 0px; }
  .suffix.active {
    color: #2196F3; }

button.material-icons.suffix {
  width: initial;
  top: -6px; }

.suffix ~ input,
.suffix ~ textarea {
  margin-right: 2rem;
  width: calc(100% - 2rem); }

.suffix ~ textarea {
  padding-top: .8rem; }

.suffix ~ label {
  margin-right: 3rem; }

input:not([type]), input[type=text], input[type=password], input[type=email], input[type=url], input[type=time], input[type=date], input[type=datetime], input[type=datetime-local], input[type=tel], input[type=number], input[type=search], textarea.materialize-textarea {
  box-sizing: border-box; }

.input-field label {
  left: 0; }

.input-field i.align-right, .input-field button.align-right {
  position: absolute;
  top: 0;
  right: 0px;
  cursor: pointer;
  line-height: 2.15em;
  padding: 0 5px; }
  .input-field i.align-right:hover, .input-field button.align-right:hover {
    color: #0c9587; }

.input-field i.align-left, .input-field button.align-left {
  position: absolute;
  top: 0;
  left: 0px;
  cursor: pointer;
  line-height: 2.15em;
  padding: 0 5px; }
  .input-field i.align-left:hover, .input-field button.align-left:hover {
    color: #0c9587; }

